<template>
	<view class="login">
		<view class="content">
			<view class="logo">
				<image class="logoimg" src="../../static/image/1.jpg" mode=""></image>
			</view>
			<view class="from">
				<uni-forms ref="formData" :modelValue="formData">
					<uni-forms-item label="手机号" name="phone" required>
						<uni-easyinput :inputBorder="false" type="text" v-model="formData.phone" placeholder="请输入手机号" />
					</uni-forms-item>
					<uni-forms-item label="密码" required name="password">
						<uni-easyinput :inputBorder="false" type="text" v-model="formData.password" placeholder="请输入密码" />
					</uni-forms-item>
				</uni-forms>
				<button class="submit" type="primary" @click="submit">登入</button>
			</view>
			<view class="bottom_util">
				<view class="" @click="phone_code_login">手机验证码</view>
				<view class=""></view>
				<view class=""></view>
				<view class=""></view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				formData: {
					phone: '',
					password: ''
				},
				rules: {
					phone: {
						rules: [{
							required: true,
							errorMessage: '手机号不能为空',
						}, {
							pattern: '^1[0-9]{10}$',
							errorMessage: '手机号不合法'
						}]
					},
					password: {
						rules: [{
							required: true,
							errorMessage: '密码不能为空',
						}, {
							validateFunction: function(rule, value, data, callback) {
								if (value.length < 6) {
									callback('密码必须为6位数')
								}
								return true
							}
						}]
					}
				}
			}
		},
		methods: {
			submit() {
				uni.showLoading()
				this.$refs.formData.validate().then(res => {
					uni.hideLoading()
					console.log('表单数据信息：', res);
				}).catch(err => {
					uni.hideLoading()
					console.log('表单错误信息：', err);
				})
			},
			phone_code_login() {
				uni.login({
					provider:'weixin'
				})
			}
		},
		onReady() {
			this.$refs.formData.setRules(this.rules)
		},
	}
</script>

<style lang="scss">
	.login {
		width: 750rpx;
		height: 100vh;

		.content {
			width: 80%;
			height: 100%;
			margin: 0 auto;
			overflow: hidden;

			.logo {
				text-align: center;
				margin-top: 100rpx;

				.logoimg {
					width: 220rpx;
					height: 160rpx;
				}
			}
			.from {
				margin-top: 100rpx;
				.uni-forms-item {
					border-bottom: 1px solid #eee;
				}
			}
			.submit {
				margin-top: 80rpx
			}
			.bottom_util {
				margin-top: 80%;
			}
		}
	}
</style>
